import { Component, OnInit } from "@angular/core";
import { FormGroup, Validators } from "@angular/forms";
import { s4AccountToMap } from "app/s4-setup/mapper/s4-account-map";
import {
  IGlAccountModel,
  IS4AccountMapGetAllModel,
  IS4AccountMapModel,
  IS4AccountMapRequest,
} from "app/s4-setup/models/s4-account-map";
import { S4AccountMapService } from "app/s4-setup/services/s4-account-map.service";
import { RadioButtonModel } from "app/shared/components/dynamic-form-field/radio-button/radio-button.component";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IResponse } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
  selector: "app-add-s4-account-map",
  templateUrl: "./add-s4-account-map.component.html",
  styleUrls: ["./add-s4-account-map.component.scss"],
})
export class AddS4AccountMapComponent extends AppHelpers implements OnInit {
  form: FormGroup;
  submitted: boolean = false;

  // s4AccountMap: IS4AccountMapModel;
  s4AccountMapId: number;

  constructor(private s4AccountMapService: S4AccountMapService) {
    super();
  }

  ngOnInit() {
    this.form = this.fb.group({
      id: [0, { nonNullable: true }],
      systemInstance_ID: [null, [Validators.required]],
      integrationPurpose_ID: [null, [Validators.required]],
      payment_ID: [null, [Validators.required]],
      variable_ID: [null, [Validators.required]],
      s4_LG_AccountCode: [null, [Validators.required]],
      s4_LG_Account_Text: [null],
      is_Aggregated: [true],
      ignoreCostCenters: [null],
      organizationTypeId: [null],
      organization_ID: [null],
      // for radio btn (job title(true) / job families(false) / none(null))
      isJobtitleType: [true],
      jobTitle_ID: [null, [Validators.required]],
      job_Title_Family_ID: [null],
      bothBankCash: [true],
      bank_ID: [null],
      is_Debit: [true],
    });

    this.activatedRoute.paramMap.subscribe((params) => {
      this.s4AccountMapId = Number(params.get("s4AccountMapId")) ?? 0;
      // if (this.s4AccountMapId > 0 && this.CheckNoBusinessOrCompany())
      //   this.getById();
    });
  }

  override onChangeBusinessAndCompanyId(): void {
    // this.s4AccountMap = null;
    if (this.s4AccountMapId > 0 && this.checkNoBusinessOrCompany())
      this.getById();
  }

  get formCtrls() {
    return this.form.controls;
  }

  getById() {
    this.showLoading();
    this.s4AccountMapService
      .getById(this.s4AccountMapId)
      .subscribe((res: IResponse<IS4AccountMapGetAllModel[]>) => {
        if (res.message.messageType == MessageTypes.Success) {
          let result: IS4AccountMapModel[] = s4AccountToMap(res.data);
          this.s4AccountMapToEdit(result[0]);
        } else this.errorList(res.message.messages);
        this.hideLoading();
      });
  }

  s4AccountMapToEdit(s4AccountMap: IS4AccountMapModel) {
    this.form.patchValue(s4AccountMap);
    // dropdown filtered by another
    this.formCtrls.integrationPurpose_ID.setValue(
      s4AccountMap.integrationPurpose_ID
    );
    this.formCtrls.variable_ID.setValue(s4AccountMap.variable_ID);
    this.formCtrls.organization_ID.setValue(s4AccountMap.organization_ID);
    setTimeout(() => {
      this.formCtrls.jobTitle_ID.setValue(s4AccountMap.jobTitle_ID);
      this.formCtrls.job_Title_Family_ID.setValue(
        s4AccountMap.job_Title_Family_ID
      );
    }, 20);
    // job radio buttons
    let isJobRadio = null;
    if (s4AccountMap.job_Title_Family_ID) isJobRadio = false;
    else if (s4AccountMap.jobTitle_ID) isJobRadio = true;
    this.formCtrls.isJobtitleType.setValue(isJobRadio);
    this.onChangeIsJobRadio(isJobRadio, true);
    // console.log(this.form.value);
  }

  submit() {
    // console.log(this.form.value);
    this.submitted = true;
    if (this.form.valid) {
      const model: IS4AccountMapRequest = {
        ID: this.form.value.id,
        SystemInstance_ID: this.form.value.systemInstance_ID,
        IntegrationPurpose_ID: this.form.value.integrationPurpose_ID,
        S4_LG_AccountCode: this.form.value.s4_LG_AccountCode,
        S4_LG_Account_Text: this.form.value.s4_LG_Account_Text,
        Payment_ID: this.form.value.payment_ID,
        Variable_ID: this.form.value.variable_ID,
        Is_Aggregated: this.form.value.is_Aggregated ?? false,
        IgnoreCostCenters: this.form.value.ignoreCostCenters ?? false,
        Organization_ID: this.form.value.organization_ID,
        JobTitle_ID: this.form.value.jobTitle_ID,
        Job_Title_Family_ID: this.form.value.job_Title_Family_ID,
        BothBankCash: this.form.value.bothBankCash ?? false,
        Bank_ID: this.form.value.bank_ID,
        Is_Debit: this.form.value.is_Debit ?? false,
      };

      model.ID == 0 ? this.create(model) : this.update(model);
    } // else this.toastr.error("General.formNotValid");
  }

  create(model: IS4AccountMapRequest) {
    this.showLoading();
    this.s4AccountMapService.create(model).subscribe((res: IResponse) => {
      if (res?.message?.messageType == MessageTypes.Success) {
        this.addSuccessfullyMsg(res?.message?.messageTypeName);
        this.changeDataAfterAddAndEdit(res.data);
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  update(model: IS4AccountMapRequest) {
    this.showLoading();
    this.s4AccountMapService.update(model).subscribe((res: IResponse) => {
      if (res.message.messageType == MessageTypes.Success) {
        this.updateSuccessfullyMsg(res?.message?.messageTypeName);
        this.changeDataAfterAddAndEdit(res.data);
      } else this.errorList(res.message.messages);
      this.hideLoading();
    });
  }

  changeDataAfterAddAndEdit(data: IS4AccountMapModel) {
    this.s4AccountMapId = data.id;
    this.formCtrls.id.setValue(this.s4AccountMapId);
    let url = `/s4-setup/${this.moduleId}/s4-account-map/edit/${this.s4AccountMapId}`;
    this.navigateTo(url);
    // this.s4AccountMap = data;
    this.formCtrls.is_Aggregated.setValue(data.is_Aggregated);
    // this.s4AccountMap.organizationTypeId =
    //   this.formCtrls.organizationTypeId.value;
  }

  clearForm() {
    // if (this.s4AccountMapId == 0)
    this.form.reset({
      id: 0,
      is_Aggregated: true,
      isJobtitleType: true,
      bothBankCash: true,
      is_Debit: true,
    });
    // else this.s4AccountMapToEdit();
    this.submitted = false;
  }

  onChangeIsJobRadio(value: boolean, firstTime: boolean = false) {
    if (!firstTime) {
      this.formCtrls.jobTitle_ID.reset();
      this.formCtrls.job_Title_Family_ID.reset();
    }
    if (value == true) {
      this.formCtrls.jobTitle_ID.setValidators([Validators.required]);
      this.formCtrls.job_Title_Family_ID.clearValidators();
    } else if (value == false) {
      this.formCtrls.job_Title_Family_ID.setValidators([Validators.required]);
      this.formCtrls.jobTitle_ID.clearValidators();
    } else {
      this.formCtrls.jobTitle_ID.clearValidators();
      this.formCtrls.job_Title_Family_ID.clearValidators();
    }
    this.formCtrls.jobTitle_ID.updateValueAndValidity();
    this.formCtrls.job_Title_Family_ID.updateValueAndValidity();
  }

  onChangeIsBankRadio(value: boolean) {
    if (!value) {
      this.formCtrls.bank_ID.setValue(null);
    }
  }

  jobTitleRadioList: RadioButtonModel[] = [
    {
      value: true,
      label: "jobTitle",
      cssClass: "col-md-4 col-12",
    },
    {
      value: false,
      label: "jobFamily",
      cssClass: "col-md-4 col-12",
    },
    {
      value: null,
      label: "none",
      cssClass: "col-md-4 col-12",
    },
  ];

  bankRadioList: RadioButtonModel[] = [
    {
      value: true,
      label: "bothBankWithInstruction",
    },
    {
      value: false,
      label: "cash",
    },
  ];

  isDebitRadioList: RadioButtonModel[] = [
    {
      value: true,
      label: "debit",
    },
    {
      value: false,
      label: "credit",
    },
  ];

  changeGLAccountText(glAccount: IGlAccountModel[]) {
    this.formCtrls.s4_LG_Account_Text.setValue(glAccount[0].GLName);
  }
}
