import { Component, OnInit } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { MatTableDataSource } from "@angular/material/table";
import { IGlAccountModel, IS4AccountMapModel } from "app/s4-setup/models/s4-account-map";
import { IS4SystemInstancePurposeModel } from "app/s4-setup/models/s4-system-instance";
import { S4AccountMapService } from "app/s4-setup/services/s4-account-map.service";
import { MessageTypes } from "app/shared/enums/message-types.enum";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppHelpers } from "app/shared/resources/app-helpers";

@Component({
	selector: "app-s4-account-map-index",
	templateUrl: "./s4-account-map-index.component.html",
	styleUrls: ["./s4-account-map-index.component.scss"],
})
export class S4AccountMapIndexComponent extends AppHelpers implements OnInit {
	form: FormGroup;
	mandatoryColomns: string[] = [
		"Ser",
		"id",
		"variables",
		"integrationPurpose",
		"s4_LG_AccountCode",
		"s4_LG_Account_Text",
		"actions",
	];
	optionalColomns: string[] = ["bothBankCash", "is_Aggregated", "ignoreCostCenters", "accountType"];

	dataSource: MatTableDataSource<IS4AccountMapModel>;
	isLoaded: boolean = false;
	constructor(private s4AccountMapService: S4AccountMapService) {
		super();
	}

	ngOnInit(): void {
		// if (this.CheckNoBusinessOrCompany()) this.getList(this.currentPage);

		this.form = this.fb.group({
			systemInstance_ID: [null],
			integrationPurpose_ID: [null],
			S4_LG_AccountCode: [null],
		});
	}

	get formCtrls() {
		return this.form.controls;
	}

	override onChangeBusinessAndCompanyId(): void {
		if (this.dataSource?.data?.length > 0) this.dataSource = new MatTableDataSource(null);
		if (this.checkNoBusinessOrCompany())
			this.getList(this.pageFromUrl > 0 ? this.pageFromUrl : this.firstPage);
	}

	searchList() {
		if (
			!this.form?.value.systemInstance_ID &&
			!this.form?.value.integrationPurpose_ID &&
			!this.form?.value.S4_LG_AccountCode
		) {
			this.warning("selectDataToSearch");
			return;
		} else {
			let filter = {
				$filter: "",
			};
			if (this.form?.value.S4_LG_AccountCode)
				filter.$filter = `S4_LG_AccountCode eq '${this.form.value.S4_LG_AccountCode}'`;
			else if (this.form?.value.integrationPurpose_ID)
				filter.$filter = `IntegrationPurpose_ID eq ${this.form.value.integrationPurpose_ID}`;
			else if (this.form?.value.systemInstance_ID)
				filter.$filter = `systemInstance_ID eq ${this.form.value.systemInstance_ID}`;
			this.getList(this.firstPage, filter);
		}
	}

	getList(page: number, filter = {}, pageSize?: number) {
		this.removePageParams();
		this.page.pageIndex = page;
		if (pageSize) this.page.pageSize = pageSize;
		let params: IPageParams = {
			PageNumber: page,
			pageSize: this.page.pageSize,
			...filter,
		};
		this.s4AccountMapService
			.getList(params)
			.subscribe((res: IResponseList<IS4AccountMapModel[]>) => {
				if (res.message.messageType == MessageTypes.Success) {
					if (res.data.length == 0 && page > 1) {
						this.getList(this.prevPage);
						return;
					}
					res.data.map((ele) => {
						ele.integrationPurpose = ele.s4_IntegrationPurpose.name;
						ele.accountType = ele.is_Debit ? "Debit" : "Credit";
						ele.variables = this.msgTranslate(ele.pay_Variable?.arName, ele.pay_Variable?.name);
						ele.bothBankCash = ele.bothBankCash && !ele.bank_ID ? true : false;
					});
					this.dataSource = new MatTableDataSource(res.data);
					this.isLoaded = true;
					this.page.length = res.meta.totalItemCount;
				} else this.errorList(res.message.messages);
			});
	}

	onDelete(s4AccountMap: IS4AccountMapModel): void {
		let msg = this.msgTranslate(
			`${s4AccountMap.id} - ${s4AccountMap.integrationPurpose} - ${s4AccountMap.accountType}`
		);
		this.deleateDialog(msg).subscribe((confirm) => {
			if (confirm) this.delete(s4AccountMap);
		});
	}

	delete(s4AccountMap: IS4AccountMapModel) {
		this.showLoading();
		this.s4AccountMapService.Delete(s4AccountMap.id).subscribe((res: IResponse) => {
			if (res.message.messageType == MessageTypes.Success) {
				this.deleteSuccessfullyMsg(res?.message?.messageTypeName);
				if (this.dataSource.data.length == 1 && this.currentPage > 1) {
					// if last item in list: get previous page
					this.getList(this.prevPage, this.page.pageSize);
				} else {
					// if not last item in list: get current page
					this.getList(this.currentPage, this.page.pageSize);
				}
			} else this.errorList(res.message.messages);
			this.hideLoading();
		});
	}

	resetSearch() {
		this.form.reset();
		this.getList(this.firstPage);
	}

	onChangePurpose(purpose: IS4SystemInstancePurposeModel[]) {
		if (purpose?.length > 0) this.formCtrls.S4_LG_AccountCode.setValue(null);
	}

	onChangeGlAccount(glAccount: IGlAccountModel[]) {
		if (glAccount?.length > 0) this.formCtrls.integrationPurpose_ID.setValue(null);
	}
}
